body {
  background-color: #eaeaea;
  padding: 0;
  margin: 0;
  font-family: "Montserrat", sans-serif;
}

input::placeholder,
textarea::placeholder {
  font-family: "Montserrat", sans-serif;
  color: #AAA;
}

input:focus,
textarea:focus {
  outline: none;
}

a {
  text-decoration: none;
  background-color: transparent;
  border: none;
  color: #000;
  transition: .4s;
  font-size: 1em;
  cursor: pointer;
  font-family: "Montserrat", sans-serif;
}

a:hover {
  color: rgb(150, 149, 149);
}

/* Form styles */
form {
  max-width: 40%;
  margin: 0 auto;
}

label {
  display: flex;
  flex-direction: column;
  margin-bottom: 1em;
}

label span {
  margin-bottom: .3em;
  font-weight: bold;
  text-align: left;
}

input,
textarea {
  box-sizing: border-box;
  border: none;
  border-bottom: 1px solid #CCC;
  padding: .8em 0;
  background-color: transparent;
}

.btn {
  background-color: #1a8918;
  color: #FFF;
  text-align: center;
  cursor: pointer;
  border-radius: 10px;
  width: 120px;
  font-weight: bold;
  border: none;
  padding: 10px 15px;
  font-size: 1em;
}

.btn.btn-dark {
  background-color: #1f1f1f;
  transition: .5s;
}

.btn.btn-danger {
  transition: .5s;
}

.btn.btn-outline {
  background-color: transparent;
  color: #1f1f1f;
  border: 1px solid #1f1f1f;
  padding: 7px 30px;
}

.btn:hover {
  background-color: #0f730c;
  color: #FFF;
}

.btn.btn-dark:hover {
  background-color: #4a4a4a;
}

.btn.btn-outline:hover {
  background-color: #1f1f1f;
}

.btn.btn-danger:hover {
  background-color: #dc3545;
}

button[disabled] {
  background-color: #AAA;
}

.container .error {
  color: #721c24;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  padding: 5px;
  border-radius: 5px;
}