.post_detail {
  margin-bottom: 2em;
}

.post_detail img {
  max-width: 600px;
}

.post_detail h2 {
  margin-bottom: .4em;
}

.post_detail .tags {
  margin-bottom: 1.2em;
  display: flex;
}

.post_detail_btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

.tags p {
  margin-right: .5em;
}

.tags span {
  font-weight: bold;
}

.createdBy {
  font-style: italic;
  color: #444;
  font-size: .8em;
  margin-bottom: 1em;
}